import React from 'react'
import styled, { keyframes } from 'styled-components'
import { Flex, Heading, Link, Button } from '@pancakeswap/uikit'
import { useWeb3React } from '@web3-react/core'
import { useTranslation } from 'contexts/Localization'
import ConnectWalletButton from 'components/ConnectWalletButton'
import PageSection from 'components/PageSection'
import useTheme from 'hooks/useTheme'

const StyledHeroSection = styled(PageSection)`
  padding: 0px;
  background: transparent;
`

const StyledMobileImage = styled.img`
  display: block;
  ${({ theme }) => theme.mediaQueries.md} {
    display: none;
  }
  border-radius: 10px;
`

const StyledDesktopImage = styled.img`
  display: none;
  ${({ theme }) => theme.mediaQueries.md} {
    display: block;
  }
  border-radius: 10px;
`

const Hero = () => {
  const { t } = useTranslation()
  const { account } = useWeb3React()
  const { theme } = useTheme()

  return (
    <>
      <StyledHeroSection
        innerProps={{ style: { padding: '0', margin: '0', width: '100%' } }}
        index={2}
        hasCurvedDivider={false}
      >
        <StyledMobileImage src="/images/farms/banner-mobile.jpg" alt="banner" />
        <StyledDesktopImage src="/images/farms/banner.jpg" alt="banner" />
      </StyledHeroSection>
    </>
  )
}

export default Hero
